.statistics {
  text-align: center;
  border: 2px solid darkgrey;
  border-radius: 5px;
  width: 400px;
  margin: 0 auto;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.6);
}

.title {
  text-transform: uppercase;
  padding: 30px;
}

.statlist {
  display: flex;
  justify-content: center;
  justify-content: space-around;
  margin: 0;
}
.item {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 80px;
}

.label {
  margin-bottom: 10px;
}
